import "./login.css"
import axios from "axios";
import { useNavigate } from "react-router-dom"

export default function Login(){
    const navigate = useNavigate();

    return <div>
        <div id="loginDiv">
            <form name="loginForm" id="loginForm" method="POST">
                <div id="loginLogo"></div>
                <input type="text" name="id" id="loginId" placeholder=""/>
                <input type="password" name="password" id="loginPassword" placeholder=""/>
                <input type="button" id="loginBtn" value="" onClick={()=>{
                    // navigate("/main", {
                    //     state : {
                    //         id : "user",
                    //         check : "checks"
                    //     }
                    // })

                    let id = document.querySelector("#loginId").value;
                    let password = document.querySelector("#loginPassword").value;
                    if(id === "" || password === "")
                        return

                    let formData = new FormData();
                    formData.append("username", id);
                    formData.append("password", password)

                    
    

                    //    
                    axios.post("/login", formData, {
                        headers : {
                            "Content-Type" : "multipart/form-data"
                        }
                    }).then((response) => {
                        if(response.status !== 200){
                            alert("  .");
                            return;
                        }else{
                            navigate("/main", {
                                state : {
                                    id : id,
                                    check : response.headers.access
                                }
                            })
                            
                        }
                    })
                    
                }}/>
                <input type="button" id="joinBtn" value="" onClick={() => {
                    navigate("/join");
                }}/>
                <a href="/search" id="searchLink">,  ?</a>
            </form>
        </div>
        <div id="illustrate">
        </div>
    </div>
}